export interface AdaptProgressModel {
  /**
   * Sets the type of the component
   */
  type: AdaptProgressType;

  /**
   * Current value of the progress
   */
  value: number;
  active?: boolean;
  completed?: boolean;
}

export enum AdaptProgressType {
  primary = 'primary',
  success = 'success',
  warning = 'warning',
  danger = 'danger',
  gray500 = 'gray-500',
  default = 'default',
}
